import styles from "./css/Login.module.css";
import { useState } from "react";
import { InputMask } from "primereact/inputmask";
import { InputText } from "primereact/inputtext";
import { Button } from "primereact/button";
import { useNavigate } from "react-router-dom";

export default function Login() {
  const navigate = useNavigate();
  const [phone, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(null);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null);

    try {
      const response = await fetch("http://127.0.0.1:8000/api/login/", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ phone, password }),
      });

      const data = await response.json();

      if (response.ok) {
        localStorage.setItem("access", data.access);
        localStorage.setItem("refresh", data.refresh);
        navigate("/office");
      } else {
        setError(data.detail || " ");
      }
    } catch (err) {
      setError("   ");
    }
  };

  return (
    <div className={`${styles.login}`}>
      <form onSubmit={handleSubmit} className={`${styles.form}`}>
        <div className={`${styles.title}`}>  </div>
        <div className={`${styles.description}`}>
          <div className={`${styles.phone}`}>
            <label htmlFor="phone" className="font-bold block mb-2">
              
            </label>
            <InputMask
              className={`${styles.customInput}`}
              id="phone"
              mask="+7 (999) 999-99-99"
              value={phone}
              onChange={(e) => setUsername(e.target.value)}
              placeholder="+7 (999) 000-00-00"
              autoClear={false}
              alwaysShowMask={true}
            ></InputMask>
          </div>
          <div className={`${styles.password}`}>
            <label htmlFor="name" className="font-bold block mb-2">
              
            </label>
            <InputText
              className={`${styles.customInput}`}
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
          </div>
        </div>
        <a href="#" className={`${styles.buttonPassword}`}>
           ?
        </a>
        <Button type="submit" className={`${styles.buttonPut}`} label="" />
        <div className={`${styles.registation}`}>
              ?{" "}
          <a
            className={`${styles.registationButton}`}
            onClick={() => navigate("/register")}
          >
            
          </a>
        </div>
        {error && <p style={{ color: "red" }}>{error}</p>}
      </form>
    </div>
  );
}
